* {
  margin: 0;
  padding: 0;
  padding-right: 0;
  font-family: "Roboto", sans-serif;
}

.MuiSelect-iconOutlined {
  color: #fff !important;
}

.fontPoppins {
  font-family: "Poppins", sans-serif !important;
}

.greenBack {
  background-color: #793fa5 !important;
  color: #fff !important;
}

.blackBack {
  background-color: #000 !important;
  color: #fff !important;
}

.lightBlackBack {
  background-color: #222222 !important;
  color: #fff !important;
}

/* NAVBAR STYLE */
.logoStyle {
  width: 120px !important;
  margin: 10px 0px !important;
}

.navbarStyle {
  background-color: #fff !important;
  color: #000 !important;
}

.nav {
  color: inherit !important;
  text-decoration: none !important;
}

.navMob {
  color: inherit !important;
  text-decoration: none !important;
}

.activeNavLink {
  color: #01975f !important;
  font-weight: 500 !important;
}

.p-15 {
  padding: 25px !important;
}

.pt-20 {
  padding-top: 20px !important;
}

.pb-20 {
  padding-bottom: 20px !important;
}

.pr-20 {
  padding-right: 20px !important;
}

.p-0 {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
}

.mt-20 {
  margin-top: 20px !important;
}

.mt-30 {
  margin-top: 30px !important;
}

.mb-10 {
  margin-bottom: 10px !important;
}

.mb-20 {
  margin-bottom: 20px !important;
}

.mb-30 {
  margin-bottom: 30px !important;
}

.mb-60 {
  margin-bottom: 60px !important;
}

.ml-10 {
  margin-left: 10px !important;
}

.fs-14 {
  font-size: 14px !important;
}

.fs-12 {
  font-size: 12px !important;
}

.lh-1p4 {
  line-height: 1.4 !important;
}

.list15pixel > .MuiListItemText-primary {
  font-size: 15px !important;
}

.mob-Dis-Block {
  display: none !important;
}

/* Banner Style */
.banner {
  height: 550px;
  width: 100%;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  margin-bottom: 50px !important;
}

.bannerCardContainer {
  height: 550px !important;
}

.bannerCardBox {
  display: flex !important;
  align-items: flex-end !important;
  height: 100% !important;
}

.bannerCard {
  height: 300px !important;
  width: 450px !important;
  background-color: #793fa5 !important;
  margin-bottom: 10px !important;
}

.bannerCardPadding {
  padding: 30px !important;
}

.bannerCardTopHeading {
  color: #fff !important;
  letter-spacing: 2px !important;
  font-weight: 500 !important;
}

.bannerCardSubHeading {
  color: #fff !important;
  margin-top: 10px !important;
  line-height: 1.7 !important;
  letter-spacing: 1px !important;
  font-weight: 200 !important;
}

.bannerButton {
  background-color: #4f5e19 !important;
  color: #fff !important;
  padding: 10px 30px !important;
  margin-top: 20px !important;
  border-radius: 5em !important;
  font-size: small !important;
  font-weight: initial !important;
  letter-spacing: 2px !important;
}

.PinkBG {
  background-color: #793fa5 !important;
  height: 300px !important;
  width: 500px !important;
}

.productImg {
  width: 100% !important;
  height: 100% !important;
}

.productListMB30 {
  margin-bottom: 30px !important;
}

.verticalButton {
  writing-mode: vertical-rl !important;
  text-orientation: sideways-right !important;
  padding-left: auto !important;
  min-height: 40% !important;
  text-align: center !important;
  padding: 15px !important;
  background-color: #793fa5 !important;
  color: #fff !important;
}

.pic-container {
  /* The width of mydocument */
  width: auto;
  margin: 0 auto;
  white-space: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
  -ms-overflow-style: -ms-autohiding-scrollbar;
}
.pic-row {
  /* As wide as it needs to be */
  width: auto;
}

.pixelhund {
  width: 300px !important;
  padding: 5px !important;
}

.aboutUsStyle1 {
  margin-bottom: 30px !important;
}

.aboutStyle1 {
  padding: 10px 0px !important;
}

.aboutStyle2 {
  padding: 10px 30px !important;
  background-color: #793fa5 !important;
  color: #fff !important;
}

.aboutDetails {
  margin-bottom: 30px !important;
}

.iconRight {
  font-size: 100px !important;
  padding: 20px 0px 20px 0px !important;
}

.productListTabs {
  padding: 30px !important;
}

.productListTabsActive {
  padding: 30px !important;
  background-color: #793fa5 !important;
}

.socialMediaHandlesStyles {
  background-color: #793fa5;
  height: 50px;
  width: 50px;
  border-radius: 50%;
  color: #fff;
  margin: 50px 10px;
}

.socialMediaHandlesStyles:hover {
  background-color: #fff;
  color: #000;
}

.footerContentStyle {
  color: #fff !important;
}

.certifyTextHead {
  margin-bottom: 60px !important;
}

.mobMb-60-30 {
  margin-bottom: 60px !important;
}

.mobMb-30-15 {
  margin-bottom: 30px !important;
}

.mobMb-30-0 {
  margin-bottom: 30px !important;
}

/* Product Category Division */
.categoryNameStyle {
  color: #fff !important;
  width: 100% !important;
  font-size: 22px !important;
}

.categoryNameStyle > fieldset {
  border: transparent !important;
}

.pink > svg {
  color: #793fa5 !important;
}

/* Search Bar Division */
.search {
  display: block;
  padding: 2px 4px;
  display: flex;
  align-items: center;
  width: 75%;
}

.searchResultCard {
  width: 25%;
  max-height: 260px;
  overflow: auto;
  position: absolute;
  top: 90px;
  background-color: #ece7f0 !important;
}

.searchResultEach {
  padding: 10px !important;
}

.searchResultEach:hover {
  background-color: #e0e6f0;
}

@media (max-width: 960px) {
  .mobMb-30-0 {
    margin-bottom: 0px !important;
  }
  .lightBlackBack {
    background-color: #fff !important;
    color: #fff !important;
  }
  .mobM-0-5 {
    margin: 5px !important;
  }
  .mobMb-60-30 {
    margin-bottom: 30px !important;
  }

  .mobLightBlackBack {
    background-color: #222222 !important;
  }

  .mobMb-30-15 {
    margin-bottom: 15px !important;
  }

  .mobCenter {
    text-align: center !important;
  }
  .productListTabs {
    padding: 20px !important;
  }

  .productListTabsActive {
    padding: 20px !important;
    background-color: #793fa5 !important;
  }

  .certifyTextHead {
    margin-bottom: 30px !important;
  }

  .certifyText {
    text-align: center !important;
    margin-bottom: 15px !important ;
  }

  .iconRight {
    font-size: 100px !important;
    padding: 0px !important;
    font-size: 75px !important;
  }

  .aboutDetails {
    margin-bottom: 0px !important;
  }

  .listMb {
    margin-bottom: 0px;
  }

  .pixelhund {
    width: 200px !important;
  }

  .productImg {
    height: 200px !important;
  }

  .verticalButton {
    display: none !important;
  }

  /* Banner style for Mobile Responsive */
  .banner {
    height: 250px !important;
    width: 100% !important;
    margin-bottom: 30px !important;
  }

  .bannerCardContainer {
    height: 250px !important;
  }

  .bannerCardBox {
    display: flex !important;
    align-items: center !important;
    justify-content: center !important;
    height: 100% !important;
  }

  .bannerCard {
    height: 80% !important;
    width: 80% !important;
    background-color: #793fa5 !important;
    margin-bottom: 5px !important;
    text-align: center;
  }

  .bannerCardPadding {
    padding: 20px !important;
  }

  .bannerCardTopHeading {
    color: #fff !important;
    letter-spacing: 2px !important;
    font-weight: 500 !important;
    font-size: 25px !important;
  }

  .bannerCardSubHeading {
    color: #fff !important;
    margin-top: 8px !important;
    line-height: 1.3 !important;
    letter-spacing: 1px !important;
    font-weight: 200 !important;
    font-size: 12px !important;
  }

  .bannerButton {
    background-color: #4f5e19 !important;
    color: #fff !important;
    padding: 10px 30px !important;
    margin-top: 15px !important;
    border-radius: 5em !important;
    font-size: 10px !important;
    font-weight: initial !important;
    letter-spacing: 2px !important;
  }

  .listPink {
    background-color: #793fa5 !important;
    margin-top: 10px !important;
  }

  .aboutUsStyle1 {
    margin-bottom: 20px !important;
    text-align: center;
  }

  .aboutStyle1 {
    text-align: center !important;
    padding: 10px 0px 0px !important;
    font-size: 28px !important;
    width: 100% !important;
  }

  .aboutStyle2 {
    background-color: #fff !important;
    color: #793fa5 !important;
    font-weight: 700 !important;
    padding: 0px 0px 10px !important;
    text-align: center !important;
    font-size: 28px !important;
  }

  .footerContentStyle {
    text-align: center;
  }

  .logoStyle {
    width: 85px !important;
  }

  .mob-Cen-Text {
    text-align: center !important;
  }

  .mob-mb-20 {
    margin-bottom: 20px !important;
  }

  .mob-Dis-None {
    display: none !important;
  }

  .mob-Dis-Block {
    display: block !important;
  }

  .searchResultCard {
    width: 75%;
    max-height: 260px;
    overflow: auto;
    position: absolute;
    top: 90px;
    background-color: #ece7f0 !important;
  }

  .productListMB30 {
    margin-bottom: 0px !important;
  }
}

.search-hover {
  border: 1px solid #ccc;
  outline: none;
  background-size: 22px;
  background-position: 13px;
  border-radius: 10px;
  width: 50px;
  height: 50px;
  padding: 25px;
  transition: all 0.5s;
}
.search-hover:hover {
  width: 300px;
  padding-left: 50px;
}
